'use client'

import constants from '@/app/utils/constants';
import { event } from "next/dist/build/output/log";

//let username = document.getElementById('username');

async function submitForm(ev) {
    ev.preventDefault();
    
    const url = `${constants.baseVismaUrl}/api/register`;
    console.log(ev.target);
    let request = new Request(url, {
        method: "POST",
        body: JSON.stringify({ username: "test10@test.com", password: "password" }),
        headers: {
            "Accept": "application/json",
            "Content-Type": "application/json;charset=utf-8",
        },
        //mode: "no-cors",
    });
    const data = await fetch(request);

    console.log("data", data);
}

export default function Register() {
    return (
        <section className="flex-auto">
            <h1>Register new User</h1>
            <div>
                <label>Email</label>
                <input id="email" type="email" name="email" required={true} placeholder="email@example.com" />
                <label>Password</label>
                <input id="password" type="password" name="password" required={true} placeholder="xxxxxx" />
                <br />
                <br />
                <input type="submit" required={true} value="Register" onSubmit={submitForm(event)} />
            </div>
        </section>
    );
}
